<template>
    <mt-tabbar fixed v-model="tabnavId">
        <mt-tab-item v-for=" item in tabnavList "  @click.native="changeTabBar( item )" :id=" item.id " >
            <i slot="icon" class="iconfont" :class=" item.icon "></i>
            {{ item.name }}{{ $route.params.id }}
        </mt-tab-item>
    </mt-tabbar>
</template>

<script>

import { Tabbar, TabItem } from 'mint-ui';

import { mapGetters, mapActions } from 'vuex';


export default 
{
    computed: mapGetters(
    {
        tabnavList: 'tabnavList',
        tabnavId: 'tabnavId',
    }),
    components: 
    {
        
    },
    methods:
    {
        changeTabBar: function( data )
        {
            this.$router.push( data.path );
            
            this.$store.dispatch('changeTabBar', data);

            this.$store.dispatch('changeHdTitle', data);
        }
    }
};
</script>



<style lang="scss" scoped>
    
    @import "../assets/styles/vendor/compass-mixins/lib/_compass";
    @import "../assets/styles/vendor/compass-mixins/lib/_animate";

    .mint-tabbar
    {
        height: 55px;

        .mint-tab-item
        {
            @include ellipsis();
            
            @include clearfix();

            &.is-selected
            {
                color: #26a2ff;
            }


            .mint-tab-item-icon
            {
                i
                {
                    display: inline-block;
                    width: 24px;
                    height: 24px;
                    font-size: 24px;
                }
                
            }
        }
    }
</style>



