import home from './components/home/home';
import Hello from './components/hello';

import cantactUs from './components/contact-us/index';

const routes = 
[
    { path: '/home', component: home },
    { path: '/home/:menu', component: home },

    { path: '/account-list/', component: Hello },
    { path: '/account-list/:id', component: Hello },
    { path: '/inner-transfer/', component: Hello },
    { path: '/mobile-transfer/:id', component: Hello },

    { path: '/contact-us/', component: cantactUs },
]


export default routes;




