import * as types from '../mutation-types'

const state = 
{
    tabnavId: "0",
    tabnavList: 
    [
        {
            id: '0',
            name: '首页',
            icon: 'icon-tabbar-home',
            path: '/home'
        },
        {
            id: '1',
            name: '手机交易查询',
            icon: 'icon-tabbar-account',
            path: '/account-list'
        },
        {
            id: '2',
            name: '行内转账',
            icon: 'icon-tabbar-transfer',
            path: '/inner-transfer'
        },
        {
            id: '3',
            name: '手机转账',
            icon: 'icon-tabbar-mobile',
            path: '/mobile-transfer'
        }
    ]
}

// getters
const getters = 
{
    tabnavId: state => state.tabnavId,
    tabnavList: state => state.tabnavList,
}

// actions
const actions = 
{
    changeTabBar: ({ commit }, data ) => 
    {
        commit(types.CHANGE_TAB_BAR, data)
    }
}

// mutations
const mutations = 
{
    [types.CHANGE_TAB_BAR] (state, { id, name }) 
    {
        state.tabnavId  = id;
    },
}

export default 
{
    state,
    getters,
    actions,
    mutations
}



