<template>
    <ul class="menu-list">
        <li class="item" v-for=" item in menuList ">
            <a @click="changeMenu( item )">
                <i class="iconfont" :class=" item.icon "></i>
                <cite>{{ item.name }}</cite>
            </a>
        </li>
    </ul>
</template>

<script>

export default 
{
    props: 
    [
        'menuList',
    ],
    methods:
    {
        changeMenu: function( data )
        {
            let path = data.path ? data.path : ('/home/' + data.id);

            this.$router.push( path );
            this.$store.dispatch('changeHdTitle', data);
        }
    }
};
</script>

<style lang="scss" scoped>
    
    @import "../../assets/styles/vendor/compass-mixins/lib/_compass";
    @import "../../assets/styles/vendor/compass-mixins/lib/_animate";
    
    .menu-list
    {
        padding: 8px;
        @include clearfix();
        .item
        {
            float: left;
            width: 25%;
            text-align: center;

            a
            {
                display: block;
                padding: 8px;
                color: #666;

                // @include ellipsis();
                i
                {
                    display: block;
                    height: 36px;
                    font-size: 36px;
                }

                cite
                {
                    min-height: 3em;
                    line-height: 1.4em;
                }
            }
        }
    }
    
</style>






