<template>
    <div>
        <menu-list :menuList="menuList" ></menu-list>
    </div>
</template>

<script>

import menuList from './menu-list';

import { mapGetters, mapActions } from 'vuex';

export default 
{

    computed: 
    {
        menuList: function()
        {
            let _menu = this.$route.params.menu || 0;

            return this.$store.getters.menuList[_menu];
        },
    },

    components: 
    {
        menuList
    },
    methods:
    {
        
    }
};
</script>





